.card {
    position: relative;
    width: 600px;
    padding: 20px;
    max-width: 85vw;
    height: 58vh;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
}
.tinderCards__containerCards{
    display: flex;
    justify-content: center;
    margin-top: 1vh;
}
.swipe{
    position: absolute;
}
.card > h3 {
    position: absolute;
    bottom: 10px;
    color: white;
}